#sideNav {
    background-color: rgb(0, 156, 143);
    
    .navbar-nav .nav-item .nav-link {
      font-weight: 800;
      letter-spacing: 0.05rem;
      text-transform: uppercase;
      
    }
    .navbar-toggler {
      &:focus {
        outline-color: lighten(#BD5D38, 15%);
      }
    }
  }
  
@media (min-width: 992px) {
    #sideNav {
      text-align: center;
      position: fixed;
      top: 0;
      left: 0;
      background-color: rgb(0, 156, 143);
  
      display: flex;
      flex-direction: column;
  
      width: 20vw;
      height: 100vh;
      
      .navbar-brand {
        display: flex;
  
        margin: auto auto 0;
        padding: 0.5rem;
        .img-profile {
          max-width: 10rem;
          max-height: 10rem;
          border: 0.5rem solid fade-out(#fff, 0.8);
        }
      }
      .navbar-collapse {
        display: flex;
        align-items: flex-start;
        flex-grow: 0;
  
        width: 100%;
        margin-bottom: auto;
        .navbar-nav {
          flex-direction: column;
  
          width: 100%;
          .nav-item {
            display: block;
            .nav-link {
              display: block;
            }
          }
        }
      }
    }
}